import React from 'react';
import {Link} from 'react-router-dom';
import queryString from 'query-string'
export class PostsList2 extends React.Component {
    state = {
        posts: []
    }

    componentDidMount() {
        fetch('https://jsonplaceholder.typicode.com/posts')
          .then(response => response.json())
          .then(posts => posts.slice(60,70))
          .then(posts => this.setState({posts}));
    }

    componentDidUpdate (prevProps) {
        if (this.props.location.search !== prevProps.location.search) {
            const values = queryString.parse(this.props.location.search)
            this.getMoreData(values.page)
        }
    }

    getMoreData(page) {
        fetch('https://jsonplaceholder.typicode.com/posts')
          .then(response => response.json())
          .then(posts => posts.slice(page * 60 - 10, page * 60))
          .then(posts => this.setState({posts}));
    }

    render() {
        const posts = this.state.posts.map(post => <div><Link to={{pathname: `/newpage/${post.id}`}}>{post.title}</Link></div>)
        return (
            <div>
                <h2>All posts 2</h2>
                {posts}
                <ul class="pagination">
                    <Link to={{ pathname: '/', search: '?page=1'}}>4</Link>
                    <Link to={{ pathname: '/', search: '?page=2'}}>5</Link>
                    <Link to={{ pathname: '/', search: '?page=3'}}>6</Link>
                </ul>
            </div>
        )
    }
}
