import React from 'react';
import { Link } from 'react-router-dom';
import {Helmet} from 'react-helmet';
export class PostDetail extends React.Component {
    state = {
        post: {body: '', title: ''}
    }

    componentDidMount() {
        fetch('https://5cef52c45660c40014948b7e.mockapi.io/posts')
            .then(response => response.json())
            .then(posts => this.setState({post: posts[this.props.match.params.id - 1]}))
    }

    render() {
        return(
            <div>
                <h2>{this.state.post.title}</h2>
                <img style={{width: '500px'}} src={this.state.post.image}></img>
                <article>{this.state.post.body}</article>
                <p><Link to="/">Back to all articles</Link></p>
                <Helmet>
                    <title>{this.state.post.title}</title>
                    <meta name="google-site-verification" content="Cig7qdZyPb9u87kA3w4s2SMG5uSpKqC2iHk1GTWbdMU"></meta>
                    <meta name="description" content={this.state.post.body.slice(0,100)} />
                    <meta property="og:type"               content="article" />
                    <meta property="og:title"              content={this.state.post.title} />
                    <meta property="og:description"        content={this.state.post.body} />
                    <meta property="og:image"              content={this.state.post.image} />
                </Helmet>
            </div>
        )
    }
}